
.md-tabs{position:relative;z-index:10;width:100%;overflow:hidden;background:#E91E63;box-shadow:0 2px 5px rgba(0,0,0,0.26)
}
.md-tabs-list{position:relative;z-index:1;overflow-x:auto;overflow-y:hidden;height:48px;display:-ms-flexbox;display:flex
}
.md-tabs-link{display:block;padding:0 12px;height:48px;line-height:48px;white-space:nowrap;color:#F8BBD0
}
.md-tabs-link.active,.md-tabs-link:hover,.md-tabs-link:focus{color:#fff
}
.md-tabs-highlight{position:absolute;bottom:0;left:0;z-index:2;height:3px;background:#fff;transition:transform .3s;-webkit-backface-visibility:hidden;backface-visibility:hidden
}


