
.scroll-load{position:absolute;top:0;left:0;bottom:0;width:100%;height:100%;overflow-x:hidden;overflow-y:auto
}
.scroll-loading{padding:8px 0;text-align:center;color:#E91E63
}
.scroll-loading i{border-radius:50%;background:#fff;box-shadow:0 1px 5px rgba(0,0,0,0.2),0 -1px 5px rgba(0,0,0,0.2);transition:transform .3s;animation:fa-spin .8s infinite linear
}
@keyframes fa-spin{
0%{transform:rotate(0deg)
}
100%{transform:rotate(359deg)
}
}


