
html{font-family:sans-serif;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
body,h1,h2,h3,h4,h5,h6,p,figure,pre,dl,dd,blockquote{margin:0
}
input,legend,input,textarea,button{padding:0
}
ul,ol,form,fieldset,th,td{margin:0;padding:0
}
article,aside,details,figcaption,figure,footer,header,hgroup,main,menu,nav,section,summary{display:block
}
audio,canvas,progress,video{display:inline-block;vertical-align:baseline
}
audio:not([controls]){display:none;height:0
}
[hidden],template{display:none
}
a{background-color:transparent
}
a:active,a:hover{outline:0
}
abbr[title]{border-bottom:1px dotted
}
ul,ol{list-style:none
}
b,strong{font-weight:bold
}
mark{background:#ff0;color:#000
}
small{font-size:80%
}
sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline
}
sup{top:-0.5em
}
sub{bottom:-0.25em
}
img,legend{border:0
}
img,input,button,label{vertical-align:middle
}
svg:not(:root){overflow:hidden
}
hr{box-sizing:content-box;height:0
}
pre{overflow:auto
}
code,kbd,pre,samp{font-family:monospace,monospace;font-size:1em
}
button,input,optgroup,select,textarea{color:inherit;font:inherit;margin:0
}
button{overflow:visible
}
button,select{text-transform:none
}
button,html input[type="button"],input[type="reset"],input[type="submit"]{-webkit-appearance:button;cursor:pointer
}
button[disabled],html input[disabled]{cursor:default
}
button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0
}
input{line-height:normal
}
input[type="number"]::-webkit-inner-spin-button,input[type="number"]::-webkit-outer-spin-button{height:auto
}
input[type="search"]{-webkit-appearance:textfield;box-sizing:content-box
}
input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration{-webkit-appearance:none
}
fieldset{border:1px solid #c0c0c0;margin:0 2px;padding:.35em .625em .75em
}
textarea{overflow:auto
}
table{border-collapse:collapse;border-spacing:0
}
::-webkit-scrollbar{display:none
}
*,*:before,*:after{box-sizing:border-box
}
html{font-size:10px;-webkit-tap-highlight-color:rgba(0,0,0,0)
}
body{font-family:"Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;font-size:14px;line-height:1.5;color:#333;background-color:#fafafa
}
input,button,select,textarea{font-family:inherit;font-size:inherit;line-height:inherit
}
a{color:#E91E63;text-decoration:none
}
a:hover,a:focus{color:#c1134e
}
a:focus{outline:thin dotted;outline:5px auto -webkit-focus-ring-color;outline-offset:-2px
}
ul.default,ol.default{padding-left:30px
}
ul.default{list-style:disc
}
ol.default{list-style:decimal
}
.clearfix:before,.clearfix:after{content:" ";display:table
}
.clearfix:after{clear:both
}
.center-block{display:block;margin-left:auto;margin-right:auto
}
.text-overflow{overflow:hidden;text-overflow:ellipsis;white-space:nowrap
}
.pull-right{float:right !important
}
.pull-left{float:left !important
}
.abs-left,.abs-right{position:absolute;top:0
}
.abs-left{left:0
}
.abs-right{right:0
}
.hide{display:none !important
}
.show{display:block !important
}
.invisible{visibility:hidden
}
.hidden{display:none !important;visibility:hidden !important
}
.affix{position:fixed
}
@font-face{font-family:'Material Icons';font-style:normal;font-weight:400;src:local('Material Icons'),local('MaterialIcons-Regular'),url(fonts/MaterialIcons.woff2) format('woff2')
}
.material-icons{font-family:'Material Icons';font-weight:normal;font-style:normal;font-size:24px;display:inline-block;line-height:1;text-transform:none;letter-spacing:normal;word-wrap:normal;white-space:nowrap;direction:ltr;-webkit-font-smoothing:antialiased;text-rendering:optimizeLegibility;-moz-osx-font-smoothing:grayscale;font-feature-settings:'liga'
}
.material-icons.md-18{font-size:18px
}
.material-icons.md-24{font-size:24px
}
.material-icons.md-36{font-size:36px
}
.material-icons.md-48{font-size:48px
}
.material-icons.md-dark{color:rgba(0,0,0,0.54)
}
.material-icons.md-dark.md-inactive{color:rgba(0,0,0,0.26)
}
.material-icons.md-light{color:#fff
}
.material-icons.md-light.md-inactive{color:rgba(255,255,255,0.3)
}


